import React from "react";
import circle from "../../src/components/img/welcome.png";
import KeyboardArrowRightIcon from '@material-ui/icons/DoubleArrow';

const Home = ()=>{
    return(
        <>
        <section id="header" className="d-flex align-items-center flex-column">
        <div className="container my-5">
            <div className="row">
                <div className="col-md-10 mx-auto py-5">
                <div className="row mob-pad">
                    <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 text-home d-flex flex-column justify-content-center mx-auto" data-aos="fade-up" data-aos-duration="700">
                        <h2>Hi I Am,</h2>
                        <h1><span style={{color:"var(--main-color)"}}>Arpit</span> Alkari</h1>
                        <p>Developer  |  Designer  |  Coder</p>
                        <div className='mt-3'>
                            <a href="#about"><button className="btn btn-default py-2 px-4">About Me <KeyboardArrowRightIcon/> </button>
                            </a>
                        </div>
                    </div>

                    <div className="col-md-6 order-1 order-lg-2 image-home text-left mx-auto" data-aos="fade-up" data-aos-duration="700">
                        <img src={circle} className="img-fluid" alt="img"/>
                    </div>
                        </div>
                </div>
            </div>
        </div>
        </section>
        </>
    );
}

export default Home;