import React from "react";
import Slider from "@material-ui/core/Slider";

const Skills = () => {
   
    return (
        <>

            <section id="Skills" className="d-flex align-items-center flex-column  Skills mb-5"   >
                <div className="text-center" >
                    <h1 className="mont-font" style={{color:"var(--main-color)"}} >Skills</h1>
                </div>
                <div className="container row" data-aos="fade-up" data-aos-duration="1000">
                <div className="col-md-6 col-10">
                    <div className="slidecontainer" >
                        <h5>C++</h5>
                       <Slider  value={"75"} aria-labelledby="continuous-slider" />
                    </div>
                    <div className="slidecontainer ">
                        <h5>C</h5>
                        <Slider  value={"70"} aria-labelledby="continuous-slider" />
                    </div>
                    <div className="slidecontainer">
                        <h5>Python</h5>
                        <Slider  value={"40"} aria-labelledby="continuous-slider" />
                    </div>

                    <div className="slidecontainer">
                        <h5>React</h5>
                        <Slider  value={"55"} aria-labelledby="continuous-slider" />
                    </div>

                    <div className="slidecontainer">
                        <h5>Adobe Photoshop</h5>
                        <Slider  value={"80"} aria-labelledby="continuous-slider" />
                    </div>
                </div>

                <div className="col-md-6 col-10">
                    <div className="slidecontainer">
                        <h5>HTML</h5>
                        <Slider  value={"90"} aria-labelledby="continuous-slider" />
                    </div>

                    <div className="slidecontainer">
                        <h5>CSS</h5>
                        <Slider  value={"85"} aria-labelledby="continuous-slider" />
                    </div>

                    <div className="slidecontainer">
                        <h5>JavaScript</h5>
                        <Slider  value={"65"} aria-labelledby="continuous-slider" />
                    </div>

                    <div className="slidecontainer">
                        <h5>NodeJs</h5>
                        <Slider  value={"50"} aria-labelledby="continuous-slider" />
                    </div>
                    <div className="slidecontainer">
                        <h5>Adobe Illustrator</h5>
                        <Slider  value={"70"} aria-labelledby="continuous-slider" />
                    </div>

                </div>

                </div>



            </section>


        </>
    );
}

export default Skills;