import React from "react";
import sukoon from "./img/sukoon.png";
import news from "./img/news.jpg";
import foto from "./img/foto.png";
import game from "./img/game.png"
import Eye from '@material-ui/icons/Visibility';
import GithubIcon from '@material-ui/icons/GitHub';
import Rentors from "./img/rentor.png";

const Projects = () => {

    const Card=(props)=>{
        return(
            <>
 <div className="card my-5" style={{width: "22rem"}} >
                            <img src={props.imgsrc} className="card-img-top" alt="sukoon" style={{borderRadius:"50px 50px 0 0"}} />
                            <div className="card-body">
                                <h5 className="card-title">{props.title}</h5>
                                <p className="card-text">{props.desc}</p>
                                {/* eslint-disable-next-line */}
                                <a href={props.link} style={{backgroundColor:"var(--main-color", color:"var(--body-color)"}} target="blank" className="btn btn-primary">View Site <Eye style={{fontSize:"17px",marginLeft:"10px"}}/></a>
                            </div>
                        </div>
            </>
        );
    }


    return (
        <>
            <section id="Projects" className="my-4 Projects container">
                <div className="text-center">
                    <h1 style={{ color: "var(--main-color)" }} className="mont-font">Projects</h1>

                    <div className="projectContainer" data-aos="fade-up" data-aos-duration="1000" >

                <Card
                imgsrc={Rentors}
                title="Rentors"
                desc="A digital platform for students and house owners for rental properties. Starting soon."
                link="https://rentors.in"
                 />
                        <Card imgsrc={sukoon}
                            title="Sukoon App"
                            desc=" 'Sukoon' is an online background noise generator designed to help you relax, focus, and tune out unpleasant sounds from your environment."
                            link="https://dk004.github.io/sukoon/"
                         />

<Card imgsrc={news}
                            title="Aaktakh"
                            desc="Provides top 10 headlines of the day and also read it out for you."
                            link="https://dk004.github.io/Akhbaar/"
                         />

<Card imgsrc={foto}
                            title="FotoShop"
                            desc="A photo searching app. It will provide 10 high resolution images related to the keyword typed in serach box."
                            link="https://dk004.github.io/fotoshop/"
                         />


<Card imgsrc={game}
                            title="Appy Bird"
                            desc="A combination of 'Angry Birds' and 'Flappy bird' games. Simple yet addictive."
                            link="https://dk004.github.io/Appy-Bird/"
                         />
                       


                    </div>

                    <p className="text-secondary ">To view all my projects visit my Github acoount. 
                            <a href="https://github.com/dk004" target="blank"><button className="btn btn-default" id="git-but">Github <GithubIcon /> </button>
                            </a>
                     </p>
                </div>
            </section>




        </>
    );
}

export default Projects;